@page {
	size: a3;
}

body {
	min-width: 992px;
}

.jkt-page {
	grid-template-rows: auto;
}

.jkt-hbf-page-header {
	display: none;
}

.jkt-page-body {
	grid-template-areas:
		"cm";
}

.jkt-page-body-header {
	display: none;
}

.jkt-page-body-main {
	padding: 0;
}

.jkt-link-arrow {
	&:after {
		content: "";
	}
}